const useUrlParams = <T>(): T => {
    return window.location.search
        .replace('?', '') //!  ?   
        .split('&')
        .reduce((prevValue, curValue) => {
            const param = curValue.split('=')
            prevValue[param[0]] = param[1]
            return prevValue
        }, {}) as T
}

export default useUrlParams
